import { getByDisplayValue } from '@testing-library/react';
import React, { Component } from 'react';
import "./App.css"
import Listvalues from './Listvalues';

class App extends Component {

  constructor(){

    super();
    this.state={
      tasks:[],
     
    }
  }
  
 

  addList=(ev)=>{
    
    // here i need to add a li item to ordered list
    // ev.preventDefault();

    this.setState((prevState)=>{
    
      let inp=document.getElementById("inp");
      // console.log(inp.value)
      localStorage.setItem("tasks",JSON.stringify([...prevState.tasks,inp.value]))
      // console.log(JSON.parse(localStorage.getItem("tasks")));

      return{

        tasks:JSON.parse(localStorage.getItem("tasks")),
       

      }
        

    })
    
      
  }

  componentDidMount(){
    this.setState((prevState)=>{
      const name = localStorage.getItem('tasks');
      if(name){
        return{

          tasks:JSON.parse(localStorage.getItem("tasks")),
         
  
        }
      }
      else{
        return{
          tasks:[...prevState.tasks]
        }
      }


     
        

    })
    
  }

  // method to delete the list item from DOM and also from the list

  del=(ev)=>{

    // to delete item from state.tasks
    this.setState((prevState)=>{


      const x=[...prevState.tasks]
      x.splice(ev.target.id,1);

      localStorage.setItem("tasks",JSON.stringify(x))



     
        
          return{
            tasks:x,

          }
    })



}

  render() {
    return (
      <div className="bod">
        
        <div className="todo">
            <div className="heading"><h2>MY TODOS <span id="pen">🖊</span> </h2></div>
            
            <input type="text" id="inp" placeholder=" Enter the next task here" name="task"/><input type="button" onClick={this.addList} value="ADD TASK" id="btn"/>
            

            <div className="tasks">
              <ol className="list" > 
            

                <Listvalues val={this.state.tasks} onDel={this.del} />

              </ol>
            </div>


        </div>


      </div>
    );
  }
}

export default App;