import React, { Component } from 'react';
import "./Listvalues.css"

class Listvalues extends Component {

    constructor(props){
        super(props);
    }


    strike(ev){

        if(ev.target.checked==true){

            let element_id=`li_${ev.target.id}`;
            let el=document.getElementById(element_id);
            el.style.textDecoration="line-through";
            el.style.color='grey';


        }
        else{
            let element_id=`li_${ev.target.id}`;
            let el=document.getElementById(element_id);
            el.style.textDecoration="none";
            el.style.color='white';

        }



    }



    



    render() {
        let arr;
        if (this.props.val != undefined){
                arr=this.props.val;
        }
        else{
            arr=[];
        }
     
        return (
            <>
                {
                    arr.map((item,index)=>{
                        return(
                            <li className="li" key={index} id={`li_${index}`}>{item}  <input onChange={this.strike} id={index} className="check" type="checkbox"/> <input id={index} value="❌" onClick={this.props.onDel} type="button"/></li>
                            
                        )
                            
                        
                    })
                
              
                
              }

                
            </>
        );
    }
}

export default Listvalues;